import React, { useState, useEffect } from 'react';
import Footer from "../../components/footer/Footer";
import { Fade } from "react-reveal";
import { Line, Doughnut, Bar, Radar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  BarElement,
  RadialLinearScale
} from 'chart.js';
import './GithubStats.css';
import { FaGithub, FaStar, FaCodeBranch, FaCode, FaExclamationCircle, FaCodePullRequest, FaCalendarAlt, FaChartLine } from 'react-icons/fa';
import { BiGitRepoForked } from 'react-icons/bi';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  RadialLinearScale,
  Title,
  Tooltip,
  Legend,
  ArcElement
);

function GithubStats(props) {
  const [githubStats, setGithubStats] = useState(null);
  const [repoStats, setRepoStats] = useState([]);
  const [loading, setLoading] = useState(true);
  const [timeRange, setTimeRange] = useState('month'); // 'week', 'month', 'year'
  const [selectedMetric, setSelectedMetric] = useState('commits'); // 'commits', 'prs', 'issues'
  const [hoveredCard, setHoveredCard] = useState(null);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const [hoveredChart, setHoveredChart] = useState(null);
  const theme = props.theme;

  useEffect(() => {
    fetchGithubData();
    
    const handleMouseMove = (e) => {
      setMousePosition({
        x: (e.clientX / window.innerWidth) * 2 - 1,
        y: (e.clientY / window.innerHeight) * 2 - 1
      });
    };

    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  const calculateCardTransform = (index) => {
    if (hoveredCard === index) {
      const rotateX = mousePosition.y * 10;
      const rotateY = mousePosition.x * 10;
      return `perspective(1000px) rotateX(${rotateX}deg) rotateY(${rotateY}deg)`;
    }
    return '';
  };

  const fetchGithubData = async () => {
    try {
      const response = await fetch('../../shared/opensource/projects.json');
      const data = await response.json();
      setGithubStats(data.data);
      setRepoStats(data.data.repositories);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching GitHub data:', error);
      setLoading(false);
    }
  };

  const languageColors = {
    JavaScript: '#f1e05a',
    Python: '#3572A5',
    HTML: '#e34c26',
    CSS: '#563d7c',
    TypeScript: '#2b7489',
    Java: '#b07219',
  };

  const getLanguageData = () => {
    const languages = {};
    repoStats.forEach(repo => {
      if (repo.language) {
        languages[repo.language] = (languages[repo.language] || 0) + 1;
      }
    });
    return {
      labels: Object.keys(languages),
      datasets: [{
        data: Object.values(languages),
        backgroundColor: Object.keys(languages).map(lang => languageColors[lang] || '#858585'),
        borderWidth: 0,
      }]
    };
  };

  const getContributionData = () => {
    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    const contributions = new Array(12).fill(0);
    
    repoStats.forEach(repo => {
      const date = new Date(repo.created_at);
      contributions[date.getMonth()]++;
    });

    return {
      labels: months,
      datasets: [{
        label: 'Contributions',
        data: contributions,
        fill: true,
        borderColor: theme.text,
        backgroundColor: `${theme.text}33`,
        tension: 0.4,
      }]
    };
  };

  const getTopRepos = () => {
    return repoStats
      .sort((a, b) => b.stargazers_count - a.stargazers_count)
      .slice(0, 5);
  };

  const getRepoStatsData = () => {
    const topRepos = getTopRepos();
    return {
      labels: topRepos.map(repo => repo.name),
      datasets: [{
        label: 'Stars',
        data: topRepos.map(repo => repo.stargazers_count),
        backgroundColor: `${theme.text}99`,
      }]
    };
  };

  // Repository Status Card Data
  const repoStatusData = {
    stars: 95,
    forks: 127,
    watchers: 965,
    contributors: 10
  };

  // Pull Request Distribution Data
  const getPullRequestsData = (shouldAnimate = false) => ({
    labels: ['Open', 'Merged', 'Closed'],
    datasets: [{
      data: [25, 45, 30],
      backgroundColor: ['#2ea44f', '#8957e5', '#d73a49'],
      borderWidth: 0,
    }]
  });

  // Issue Distribution Data
  const getIssuesData = (shouldAnimate = false) => ({
    labels: ['Open', 'Closed'],
    datasets: [{
      data: [30, 70],
      backgroundColor: ['#2ea44f', '#d73a49'],
      borderWidth: 0,
    }]
  });

  // Enhanced Language Data with Skill Level
  const getLanguageSkillData = () => ({
    labels: ['JavaScript', 'Python', 'HTML/CSS', 'React', 'Node.js', 'TypeScript'],
    datasets: [{
      label: 'Skill Level',
      data: [90, 85, 95, 88, 82, 78],
      backgroundColor: `${theme.text}33`,
      borderColor: theme.text,
      pointBackgroundColor: theme.text,
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: theme.text
    }]
  });

  // Activity Heatmap Data
  const getActivityData = () => {
    const today = new Date();
    const data = [];
    for (let i = 0; i < 30; i++) {
      const date = new Date(today);
      date.setDate(date.getDate() - i);
      data.unshift({
        date: date.toISOString().split('T')[0],
        count: Math.floor(Math.random() * 10)
      });
    }
    return data;
  };

  // Enhanced Commit Activity Data with Time Range
  const getCommitActivityData = () => {
    let labels, currentData, previousData;

    switch(timeRange) {
      case 'week':
        labels = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'];
        currentData = Array.from({ length: 7 }, () => Math.floor(Math.random() * 20 + 5));
        previousData = Array.from({ length: 7 }, () => Math.floor(Math.random() * 20 + 5));
        break;
      case 'year':
        labels = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        currentData = Array.from({ length: 12 }, () => Math.floor(Math.random() * 100 + 20));
        previousData = Array.from({ length: 12 }, () => Math.floor(Math.random() * 100 + 20));
        break;
      default: // month
        labels = Array.from({ length: 30 }, (_, i) => i + 1);
        currentData = Array.from({ length: 30 }, () => Math.floor(Math.random() * 40 + 10));
        previousData = Array.from({ length: 30 }, () => Math.floor(Math.random() * 40 + 10));
    }

    return {
      labels,
      datasets: [
        {
          label: 'Current Period',
          data: currentData,
          borderColor: theme.accentColor,
          backgroundColor: `${theme.accentColor}33`,
          fill: true,
          tension: 0.4,
        },
        {
          label: 'Previous Period',
          data: previousData,
          borderColor: `${theme.text}66`,
          backgroundColor: `${theme.text}11`,
          fill: true,
          tension: 0.4,
        }
      ]
    };
  };

  // Code Quality Metrics
  const getCodeQualityData = () => ({
    labels: ['Code Coverage', 'Test Pass Rate', 'Code Review Rate', 'Documentation', 'Performance'],
    datasets: [{
      label: 'Current Status',
      data: [92, 98, 85, 78, 88],
      backgroundColor: `${theme.accentColor}33`,
      borderColor: theme.accentColor,
      borderWidth: 2,
    }]
  });

  const renderTimeRangeSelector = () => (
    <div className="time-range-selector">
      <button 
        className={timeRange === 'week' ? 'active' : ''} 
        onClick={() => setTimeRange('week')}
      >
        Week
      </button>
      <button 
        className={timeRange === 'month' ? 'active' : ''} 
        onClick={() => setTimeRange('month')}
      >
        Month
      </button>
      <button 
        className={timeRange === 'year' ? 'active' : ''} 
        onClick={() => setTimeRange('year')}
      >
        Year
      </button>
    </div>
  );

  const renderMetricSelector = () => (
    <div className="metric-selector">
      <button 
        className={selectedMetric === 'commits' ? 'active' : ''} 
        onClick={() => setSelectedMetric('commits')}
      >
        Commits
      </button>
      <button 
        className={selectedMetric === 'prs' ? 'active' : ''} 
        onClick={() => setSelectedMetric('prs')}
      >
        Pull Requests
      </button>
      <button 
        className={selectedMetric === 'issues' ? 'active' : ''} 
        onClick={() => setSelectedMetric('issues')}
      >
        Issues
      </button>
    </div>
  );

  if (loading) {
    return (
      <div className="github-stats-main" style={{ color: theme.text }}>
        <div className="loading">Loading GitHub Statistics...</div>
      </div>
    );
  }

  return (
    <div className="github-stats-main" style={{ color: theme.text }}>
      <Fade bottom duration={1000} distance="20px">
        <div className="github-stats-header">
          <FaGithub className="github-icon" />
          <h1>GitHub Statistics</h1>
        </div>

        {/* Status Cards with Enhanced 3D Effect */}
        <div className="repo-status-grid">
          {[
            { icon: FaStar, label: 'Stars', value: repoStatusData.stars },
            { icon: BiGitRepoForked, label: 'Forks', value: repoStatusData.forks },
            { icon: FaCodeBranch, label: 'Watchers', value: repoStatusData.watchers },
            { icon: FaCode, label: 'Contributors', value: repoStatusData.contributors }
          ].map((item, index) => (
            <div 
              key={index}
              className={`status-card ${hoveredCard === index ? 'hovered' : ''}`}
              onMouseEnter={() => setHoveredCard(index)}
              onMouseLeave={() => setHoveredCard(null)}
              style={{ 
                transform: calculateCardTransform(index),
                background: `linear-gradient(135deg, 
                  rgba(255, 255, 255, ${hoveredCard === index ? '0.1' : '0.05'}) 0%, 
                  rgba(255, 255, 255, ${hoveredCard === index ? '0.05' : '0.02'}) 100%)`
              }}
            >
              <div className="status-icon">
                <item.icon style={{ 
                  color: hoveredCard === index ? 'var(--gradient-1)' : 'inherit',
                  filter: hoveredCard === index ? 'drop-shadow(0 0 8px rgba(0, 242, 254, 0.5))' : 'none'
                }} />
              </div>
              <div className="status-info">
                <h3>{item.label}</h3>
                <p className="value-animation">{item.value}</p>
              </div>
              <div className="card-background" />
            </div>
          ))}
        </div>

        {/* Time Range and Metric Selectors */}
        <div className="controls-container">
          {renderTimeRangeSelector()}
          {renderMetricSelector()}
        </div>

        {/* Enhanced Stats Grid */}
        <div className="stats-grid">
          <div className="stats-card wide">
            <h3>Activity Overview</h3>
            {renderTimeRangeSelector()}
            <div className="chart-container">
              <Line 
                data={getCommitActivityData()}
                options={{
                  responsive: true,
                  plugins: {
                    legend: {
                      labels: { color: theme.text }
                    },
                    tooltip: {
                      mode: 'index',
                      intersect: false,
                    }
                  },
                  scales: {
                    y: {
                      ticks: { color: theme.text },
                      grid: { color: `${theme.text}22` }
                    },
                    x: {
                      ticks: { color: theme.text },
                      grid: { color: `${theme.text}22` }
                    }
                  },
                  interaction: {
                    mode: 'nearest',
                    axis: 'x',
                    intersect: false
                  }
                }}
              />
            </div>
          </div>

          <div className="stats-card">
            <h3>Language Skills</h3>
            <div className="chart-container">
              <Radar 
                data={getLanguageSkillData()}
                options={{
                  scales: {
                    r: {
                      angleLines: { color: `${theme.text}22` },
                      grid: { color: `${theme.text}22` },
                      pointLabels: { color: theme.text },
                      ticks: { color: theme.text }
                    }
                  }
                }}
              />
            </div>
          </div>

          <div className="stats-card">
            <h3>Contributions</h3>
            <div className="contributions-container">
              <div 
                className="contribution-chart"
                onMouseEnter={() => setHoveredChart('pr')}
                onMouseLeave={() => setHoveredChart(null)}
              >
                <h4>Pull Request Distribution</h4>
                <div className="chart-wrapper">
                  <Doughnut 
                    key={`pr-${hoveredChart === 'pr'}`}
                    data={getPullRequestsData()}
                    options={{
                      plugins: {
                        legend: {
                          position: 'bottom',
                          labels: { 
                            color: theme.text,
                            padding: 20,
                            font: {
                              size: 12
                            }
                          }
                        }
                      },
                      animation: {
                        animateScale: hoveredChart === 'pr',
                        animateRotate: hoveredChart === 'pr',
                        duration: 1000,
                        easing: 'easeInOutQuart'
                      },
                      cutout: hoveredChart === 'pr' ? '65%' : '60%',
                      transitions: {
                        active: {
                          animation: {
                            duration: 300
                          }
                        }
                      }
                    }}
                  />
                </div>
              </div>
              <div 
                className="contribution-chart"
                onMouseEnter={() => setHoveredChart('issues')}
                onMouseLeave={() => setHoveredChart(null)}
              >
                <h4>Issue Distribution</h4>
                <div className="chart-wrapper">
                  <Doughnut 
                    key={`issues-${hoveredChart === 'issues'}`}
                    data={getIssuesData()}
                    options={{
                      plugins: {
                        legend: {
                          position: 'bottom',
                          labels: { 
                            color: theme.text,
                            padding: 20,
                            font: {
                              size: 12
                            }
                          }
                        }
                      },
                      animation: {
                        animateScale: hoveredChart === 'issues',
                        animateRotate: hoveredChart === 'issues',
                        duration: 1000,
                        easing: 'easeInOutQuart'
                      },
                      cutout: hoveredChart === 'issues' ? '65%' : '60%',
                      transitions: {
                        active: {
                          animation: {
                            duration: 300
                          }
                        }
                      }
                    }}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Activity Calendar */}
        <div className="activity-calendar">
          <h3>Activity Calendar</h3>
          <div className="calendar-grid">
            {getActivityData().map((day, index) => (
              <div 
                key={index}
                className="calendar-day"
                style={{
                  backgroundColor: `${theme.accentColor}${Math.min(day.count * 20, 99)}`,
                  '--tooltip-content': `"${day.date}: ${day.count} contributions"`
                }}
                title={`${day.date}: ${day.count} contributions`}
              />
            ))}
          </div>
        </div>

        <div className="repos-grid">
          {getTopRepos().map(repo => (
            <div key={repo.id} className="repo-card" style={{ borderColor: theme.text + '33' }}>
              <h3>{repo.name}</h3>
              <p>{repo.description}</p>
              <div className="repo-stats">
                <span><FaStar /> {repo.stargazers_count}</span>
                <span><BiGitRepoForked /> {repo.forks_count}</span>
                <span><FaCode /> {repo.language}</span>
              </div>
              <a href={repo.html_url} target="_blank" rel="noopener noreferrer" className="repo-link">
                View Repository
              </a>
            </div>
          ))}
        </div>
      </Fade>
    </div>
  );
}

export default GithubStats; 